<template>
	<div class="swiper-box">
		<a-carousel >
			<img v-for="(item, index) of pageState.swiper" :key="index" :src='item' alt="">
		</a-carousel>
	</div>
</template>

<script>
import { defineComponent, onMounted, watchEffect, ref } from 'vue'
export default defineComponent({
	props: {
		list: Array
	},
	setup (props) {
		const pageState = ref({
			swiper: []
		})
		watchEffect(() => {
			pageState.value.swiper = props.list
		})
		onMounted(() => {
			pageState.value.swiper = props.list
		})
		return {
			pageState
		}
	}
})
</script>

<style scoped>
	.swiper-box {
		height: calc(100% - 80px)
	}
	.ant-carousel {
		height: 100%;
	}
	:deep(.ant-carousel .slick-slider) {
		height: 100% !important;
	}
	:deep(.slick-list) {
		height: 100% !important;
	}
	:deep(.slick-track) {
		height: 100% !important;
	}
	:deep(.slick-current) {
		height: 100% !important;
	}
	:deep(.slick-current div) {
		height: 100% !important;
	}
	.slick-current div img {
		height: 100% !important;
	}
</style>