export const filterImgSrc = (content) => {
	if (!content) return false
	//匹配图片中的img标签
	let imgReg = /<img.*?(?:>|\/>)/gi;
	// 匹配图片中的src
	let srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
	//筛选出所有的img
	let arr = content.match(imgReg);
	let srcArr = [];
	if (arr && arr.length > 0) {
		for (let i = 0; i < arr.length; i++) {
			const src = arr[i].match(srcReg);
			// 获取图片地址中参数s
			const index = src[1].lastIndexOf('name=');
			const param = src[1].substring(index + 5, src[1].length);
			srcArr.push(param);
		}
	}
	return srcArr;
}

export const getText = (str) => {
	if (!str) return false
	return str.replace(/<[^<>]+>/g, "").replace(/&nbsp;/gi, "")
}

export const getTextByP = (str) => {
	if (!str) return false
	return str.replace(/<[^p]+>/g, "").replace(/&nbsp;/gi, "")
}

export const  getParentId = (id, data = JSON.parse(localStorage.getItem('navList')), propName = 'menuId', parentPropName = 'menuId') => {
	for (let i = 0; i < data.length; i++) {
    const item = data[i];
		if (Array.isArray(item.childs) && item.childs.length > 0) {
			let filter = item.childs.filter(item => { return item.menuId == id })[0]
			let index = item.childs.findIndex(item => { return item.menuId == id })
			if (filter) return { id: item.menuId, type: i, name: item.menuName, index: index }
		}
  }
  return null;
}