<template>
	<Swiper :list="pageState.swiper"/>
	<!-- 公司介绍 -->
	<Introduce :data="pageState.introduce"/>
	<!-- 新闻资讯 -->
	<News :data="pageState.news" />
	<!-- 客户案例 -->
	<Case :data="pageState.case" />
	<Bussiness :data="pageState.bussiness" />
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
import Swiper from '@/components/swiper.vue'
import Introduce from './introduce.vue'
import News from './news.vue'
import Case from './case.vue'
import Bussiness from './business.vue'
import eventBus from '@/utils/eventBus.js'
import { selectMenuList, selectConfigPage } from '@/api/api.js'
export default defineComponent({
	components: {
		Swiper,
		Introduce,
		News,
		Case,
		Bussiness
	},
	setup () {
		const route = useRoute()
		const pageState = ref({
			introduce: {},
			news: {},
			case: {},
			bussiness: {}
		})
		onMounted(() => {
			if (route.path == '/') {
				localStorage.setItem('nav', 0)
				eventBus.$emit('changeNav', 0)
			}
			loadMenu()
		})
		const loadMenu = () => {
			selectMenuList().then((res) => {
				console.log(res)
				pageState.value.swiper = JSON.parse(res.data[0].urls).length ? JSON.parse(res.data[0].urls) : ['@/assets/default-img.png']
				if (res.code === 200) {
					selectConfigPage(res.data[0].menuId).then((res) => {
						if (res.code === 200) {
							pageState.value.introduce = { 
								title: res.data[0].contentValue,
								menuId: res.data[1].contentValue
							}
							pageState.value.news = {
								title: res.data[2].contentValue,
								menuId: res.data[3].contentValue
							}
							pageState.value.case = {
								title: res.data[4].contentValue,
								menuId: res.data[5].contentValue
							}
							pageState.value.bussiness = {
								title: res.data[6].contentValue,
								menuId: res.data[7].contentValue
							}
						}
					})
				}
			})
		}
		return {
			pageState
		}
	}
})
</script>

