<template>
	<div class="item-box">
		<div class="content-box">
			<!-- 标题 -->
			<div class="content-title">
				<span>{{pageState.firstTitle}}</span><span>{{pageState.secondTitle}}</span>
			</div>
			<!-- 内容 -->
			<div class="business-box">
				<div class="business-show flex-row">
					<div>
						<span>{{pageState.detail.articleTitle}}</span>
						<span>{{pageState.detail.desc}}</span>
						<span class="detail-more" @click="goDetail(pageState.detail.articleId)">查看详情</span>
					</div>
					<img :src="pageState.detail.thumb" alt="">
				</div>
				<!-- 切换 -->
				<div class="tab-box flex-row">
					<div 
						:class="pageState.active == index ? 'tab-item active' : 'tab-item'" 
						v-for="(item, index) of pageState.tabs" 
						:key="index"
						@click="onNavClick(index, item)"
					>
						{{item.menuName}}
					</div>
				</div>
			</div>
			<!-- 查看更多 -->
			<p class="more-box" @click="onMore">
				<span>查看更多</span>
				<img src="@/assets/right.png" alt="">
			</p>
		</div>
	</div>
</template>

<script>
import { defineComponent, watchEffect, ref } from 'vue'
import { useRouter } from 'vue-router'
import eventBus from '@/utils/eventBus.js'
import { selectMenuList, selectArticleDetailByMenuId } from '@/api/api.js'
import { filterImgSrc, getText , getParentId} from '@/utils/tools.js'
export default defineComponent({
	props: {
		data: Object
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			tabs: [ '国际工程承包', '固废处理业务', '水处理业务', '技术装备业务' ],
			active: 0,
			firstTitle: null,
			secondTitle: null,
			menuId: null,
			content: null,
			detail: {}
		})
		watchEffect(() => {
			pageState.value.firstTitle = props.data.title ? props.data.title.slice(0, 2) : null
			pageState.value.secondTitle = props.data.title ? props.data.title.slice(2, props.data.title.length) : null
			pageState.value.menuId = props.data.menuId
			if (props.data.menuId) {
				selectMenuList(
					{	
						menuId: props.data.menuId,
						language: 'en'
					}
				).then((res) => {
					if (res.code === 200) {
						pageState.value.tabs = res.data
						loadData(pageState.value.tabs[0].menuId)
					}
				})
			}
		})
		const onNavClick = (index, item) => {
			pageState.value.active = index
			loadData(item.menuId)
		}
		const loadData = (id) => {
			selectArticleDetailByMenuId(id).then((res) => {
				if (res.code === 200) {
					res.data.desc = getText(res.data.content)
					res.data.thumb = res.data.urls && JSON.parse(res.data.urls).length ? JSON.parse(res.data.urls)[0] : require('@/assets/default-img.png')
					pageState.value.detail = res.data
				}
			})
		}
		const goDetail = (articleId) => {
			localStorage.setItem('contentData', JSON.stringify(getParentId(pageState.value.tabs[pageState.value.active].menuId)) )
			localStorage.setItem('nav', getParentId(pageState.value.tabs[pageState.value.active].menuId).type )
			localStorage.setItem('type', pageState.value.active )
			eventBus.$emit('changeNav', getParentId(pageState.value.tabs[pageState.value.active].menuId).type)
			router.push({
				path: '/detail',
				query: { menuId:pageState.value.tabs[pageState.value.active].menuId}
			})
		}
		return {
			pageState,
			onNavClick,
			goDetail
		}
	}
})
</script>

