<template>
	<div class="item-box">
		<div class="content-box">
			<!-- 标题 -->
			<div class="content-title">
				<span>{{pageState.firstTitle}}</span><span>{{pageState.secondTitle}}</span>
			</div>
			<!-- 内容 -->
			<div class="news-box flex-row">
				<div class="big-news">
					<img :src="pageState.top.thumb" alt="" @click="goDetail(pageState.top.articleId)"/>
					<p>{{pageState.top.articleTitle}}</p>
				</div>
				<div class="news-list">
					<div class="news-item flex-row" v-for="(item, index) of pageState.list" :key="index"  @click="goDetail(item.articleId)">
						<div>
							<p>{{item.year}}</p>
							<p>{{item.time}}</p>
						</div>
						<div>
							<p>{{item.articleTitle}}</p>
							<p>{{item.desc}}</p>
						</div>
					</div>
				</div>
			</div>
			<!-- 查看更多 -->
			<p class="more-box" @click="onMore">
				<span>查看更多</span>
				<img src="@/assets/right.png" alt="">
			</p>
		</div>
	</div>
</template>

<script>
import { defineComponent, watchEffect, ref } from 'vue'
import { useRouter } from 'vue-router'
import { selectArticleListByMenuId } from '@/api/api.js'
import eventBus from '@/utils/eventBus.js'
import { filterImgSrc, getText, getParentId } from '@/utils/tools.js'
export default defineComponent({
	props: {
		data: Object
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			firstTitle: null,
			secondTitle: null,
			menuId: null,
			list: [],
			top: {}
		})
		watchEffect(() => {
			pageState.value.firstTitle = props.data.title ? props.data.title.slice(0, 2) : null
			pageState.value.secondTitle = props.data.title ? props.data.title.slice(2, props.data.title.length) : null
			pageState.value.menuId = props.data.menuId
			if (props.data.menuId) {
				let data = {
					menuId: props.data.menuId,
					pageNum: 1,
					pageSize: 5
				}
				selectArticleListByMenuId(data).then((res) => {
					if (res.code === 200) {
						pageState.value.list = res.data.records.map(item => ({
							...item,
							year: item.createTime.slice(0, 4),
							time: item.createTime.slice(5, item.createTime.length),
							desc: getText(item.content),
							thumb: JSON.parse(item.urls).length ? JSON.parse(item.urls)[0] : require('@/assets/default-img.png')
						}))
						pageState.value.top = pageState.value.list[0]
					}
				})
			}
		})
		const goDetail = (articleId) => {
			localStorage.setItem('contentData', JSON.stringify(getParentId(pageState.value.menuId)) )
			localStorage.setItem('nav', getParentId(pageState.value.menuId).type )
			localStorage.setItem('type', getParentId(pageState.value.menuId).index )
			eventBus.$emit('changeNav', getParentId(pageState.value.menuId).type)
			router.push({
				path: '/detail',
				query: { articleId: articleId, menuId: pageState.value.menuId  }
			})
		}
		const onMore = () => {
			localStorage.setItem('contentData', JSON.stringify(getParentId(pageState.value.menuId)) )
			localStorage.setItem('nav', getParentId(pageState.value.menuId).type )
			localStorage.setItem('type', getParentId(pageState.value.menuId).index )
			eventBus.$emit('changeNav', getParentId(pageState.value.menuId).type)
			router.push({
				path: '/news',
				query: { menuId: pageState.value.menuId }
			})
		}
		return {
			pageState,
			onMore,
			goDetail
		}
	}
})
</script>

