<template>
	<div class="item-box">
		<div class="content-box">
			<!-- 标题 -->
			<div class="content-title">
				<span>{{pageState.firstTitle}}</span><span>{{pageState.secondTitle}}</span>
			</div>
			<!-- <img class="content-title" src="@/assets/index-title1.png" alt="公司介绍标题"> -->
			<!-- 内容 -->
			<div class="introduce-box flex-row">
				<img :src="pageState.thumb" alt="公司介绍图片">
				<div class="introduce-text">
					<p>山西国投日昇环境治理有限公司</p>
					<p v-html="pageState.content"></p>
					<p @click="onMore">
						<span>查看更多</span>
						<img src="@/assets/right.png" alt="">
					</p>
				</div>
			</div>
			<!-- 数据 -->
			<div class="grid-box flex-row">
				<div class="grid-item">
					<p>
						<span>2017</span>
						<span>年</span>
					</p>
					<p>公司创立于2017年</p>
				</div>
				<div class="grid-item">
					<p>
						<span>5000</span>
						<span>万</span>
					</p>
					<p>注册资本5000万元</p>
				</div>
				<div class="grid-item">
					<p>
						<span>30</span>
						<span>名</span>
					</p>
					<p>专业技术人员30名</p>
				</div>
				<div class="grid-item">
					<p>
						<span>100+</span>
						<span>项</span>
					</p>
					<p>典型业绩100余项</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent, watchEffect, ref } from 'vue'
import { useRouter } from 'vue-router'
import eventBus from '@/utils/eventBus.js'
import { selectArticleDetailByMenuId, selectArticleListByMenuId } from '@/api/api.js'
import { filterImgSrc, getTextByP , getParentId} from '@/utils/tools.js'
export default defineComponent({
	props: {
		data: Object
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			firstTitle: null,
			secondTitle: null,
			menuId: null,
			content: null
		})
		watchEffect(() => {
			pageState.value.firstTitle = props.data.title ? props.data.title.slice(0, 2) : null
			pageState.value.secondTitle = props.data.title ? props.data.title.slice(2, props.data.title.length) : null
			pageState.value.menuId = props.data.menuId
			if (props.data.menuId) {
				selectArticleDetailByMenuId(props.data.menuId).then((res) => {
					if (res.code === 200) {
						pageState.value.thumb = filterImgSrc(res.data.content) ? `http${filterImgSrc(res.data.content)[0]}` : '@/assets/swiper.png'
						pageState.value.content = getTextByP(res.data.content)
					}
				})
			}
		})
		const onMore = () => {
			localStorage.setItem('contentData', JSON.stringify(getParentId(pageState.value.menuId)) )
			localStorage.setItem('nav', getParentId(pageState.value.menuId).type )
			localStorage.setItem('type', getParentId(pageState.value.menuId).index )
			eventBus.$emit('changeNav', getParentId(pageState.value.menuId).type)
			router.push({
				path: '/detail',
				query: { menuId: pageState.value.menuId}
			})
		}
		return {
			pageState,
			onMore
		}
	}
})
</script>

